import React from 'react';
import { IconButton, Avatar, Box, CloseButton, Flex, HStack, VStack, Icon, useColorModeValue, Link, Drawer, DrawerContent, Text, useDisclosure, Button, Image } from '@chakra-ui/react';
import { FiHome, FiTrendingUp, FiCompass, FiStar, FiSettings, FiMenu } from 'react-icons/fi';
import s from '../images/logoCheck1.jpg';
import CV from "../images/CV.pdf"

const LinkItems = [
  { name: 'Home', icon: FiHome, class: "nav-link home", l: "#home" },
  { name: 'About', icon: FiStar, class: "nav-link about", l: "#about" },
  { name: 'Skills', icon: FiCompass, class: "nav-link skills", l: "#skills" },
  { name: 'Projects', icon: FiTrendingUp, class: "nav-link projects", l: "#projects" },
  { name: 'Contacts', icon: FiSettings, class: "nav-link contact", l: "#contact" },
  // {name:'Resume',icon:FiStar,class:"nav-link resume",l:"https://drive.google.com/file/d/1m-5h1Is4mNkilVLdVOFL7cg8Pegx4CkL/view?usp=share_link"}
];

export default function SidebarWithHeader() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <Box id="nav-menu">
      <SidebarContent
        onClose={() => onClose}
        display={{ base: 'none', md: 'block' }}
      />
      {/* <Box bg={useColorModeValue('gray.100', 'gray.900')} border="1px solid" > */}
      <Drawer
        autoFocus={false}
        isOpen={isOpen}
        placement="left"
        onClose={onClose}
        returnFocusOnClose={false}
        onOverlayClick={onClose}
        size="full">
        <DrawerContent>
          <SidebarContent onClose={onClose} />
        </DrawerContent>
      </Drawer>
      <MobileNav onOpen={onOpen} />

      {/* </Box> */}
    </Box>

  );
}

const SidebarContent = ({ onClose, ...rest }) => {
  return (
    <Box
      display="block"
      // border={"1px solid"}
      bg={useColorModeValue('white', 'gray.900')}
      borderRight="1px"
      borderRightColor={useColorModeValue('gray.200', 'gray.700')}
      w={{ base: 'full', md: 60 }}
      position="fixed"
      h="full"
      {...rest}
    >
      <Flex h="20" alignItems="center" mx="8" justifyContent="space-between">
        <Text fontSize="2xl" fontFamily="monospace" fontWeight="bold" margin>
          <Image src={s} />
        </Text>
        <CloseButton display={{ base: 'flex', md: 'none' }} onClick={onClose} />
      </Flex>
      {LinkItems.map((link) => (
        <NavItem key={link.name} icon={link.icon} className={link.class} to={link.l}>
          {link.name}
        </NavItem>
      ))}
    </Box>
  );
};

const NavItem = ({ icon, className, to, children, ...rest }) => {
  return (
    <Link href={to} className={className}
      style={{ textDecoration: 'none' }}
      _focus={{ boxShadow: 'none' }}
    >
      <Flex
        align="center"
        p="3"
        mx="5"
        borderRadius="lg"
        role="group"
        cursor="pointer"
        fontSize="19px"
        marginTop="2rem"
        bg="blue.500"
        color='white'
        rounded={'full'}
        boxShadow={
          '0px 1px 25px -5px rgb(66 153 225 / 48%), 0 10px 10px -5px rgb(66 153 225 / 43%)'
        }
        _hover={{
          bg: 'blue.50',
          color: 'blue.500',
          border: "1px"
        }}
        {...rest}
      >
        {icon && (
          <Icon
            mr="4"
            fontSize="20px"
            _groupHover={{
              color: 'blue.500',
            }}
            as={icon}
          />
        )}
        {children}
      </Flex>
    </Link>
  );
};

const MobileNav = ({ onOpen, ...rest }) => {

  function resume() {
    window.open(
      "https://drive.google.com/file/d/1m-5h1Is4mNkilVLdVOFL7cg8Pegx4CkL/view?usp=share_link",
      "_blank"
    );
  }


  return (
    <Flex
      ml={{ base: 0, md: 60 }}
      px={{ base: 5, md: 4 }}
      height="20"
      alignItems="center"
      bg={useColorModeValue('white', 'gray.900')}
      borderBottomWidth="1px"
      borderBottomColor={useColorModeValue('gray.100', 'gray.700')}
      justifyContent={{ base: 'space-between', md: 'flex-end' }}
      {...rest}>
      <IconButton
        display={{ base: 'flex', md: 'none' }}
        onClick={onOpen}
        variant="outline"
        aria-label="open menu"
        icon={<FiMenu />}
      />

      <Image
        src={s}
        display={{ base: 'flex', md: 'none' }}
        fontSize="2xl"
        fontFamily="monospace"
        fontWeight="bold"
        width="120px"
        height="39px"
      />

      <HStack spacing={{ base: '0', md: '6' }}>
        <VStack>
          {/* <Link display={{ base: 'none', md: 'flex' }}
                className="nav-link resume"
                style={{ textDecoration: 'none',margin:"2rem" }} alignItems="flex-start" spacing="1px"
                href={CV}
                // id="resume-link-1"
                download={"Shaifali-Resume"}
                onClick={()=>window.open("https://drive.google.com/file/d/1m-5h1Is4mNkilVLdVOFL7cg8Pegx4CkL/view?usp=share_link")}
                 ml="2">
                    <Button id="resume-button-1"
                      //  {...props}
                        // flex={1} 
                        px={8}
                        py={6}
                        marginRight={'2rem'}
                        fontSize={'18px'}
                        rounded={'full'}
                        bg={'blue.400'}
                        color={'white'}
                        boxShadow={
                          '0px 1px 25px -5px rgb(66 153 225 / 48%), 0 10px 10px -5px rgb(66 153 225 / 43%)'
                        }
                        _hover={{
                          bg: 'blue.50',
                          color:'blue.500',
                          border:'1px'
                        }}
                        _focus={{
                          bg: 'blue.500',
                        }}>
                      Resume
                    </Button>
                        
                    </Link> */}


          <Button id="resume-button-1" 
           className="nav-link resume"
           onClick={resume}
           _hover={{
            bg: 'blue.50',
            color:'blue.500',
            border:'1px'
          }}
          _focus={{
            bg: 'blue.500',
          }}
          px={8}
          py={6}
          marginRight={'2rem'}
          fontSize={'18px'}
          rounded={'full'}
          bg={'blue.400'}
          color={'white'}
          boxShadow={
            '0px 1px 25px -5px rgb(66 153 225 / 48%), 0 10px 10px -5px rgb(66 153 225 / 43%)'
          }
            >
            <a
              id="resume-link-1"
              href={CV}
              download="Shaifali-Resume"
              target="_blank"

            >
              RESUME
            </a>
          </Button>

        </VStack>
        <VStack></VStack>
        <HStack>
          <Avatar
            size={'md'}
            src={'https://avatars.githubusercontent.com/u/112679991?v=4'}
          />
          <VStack
            display={{ base: 'none', md: 'flex' }}
            alignItems="flex-start"
            spacing="1px"
            ml="4">
            <Text fontSize="md" mr="2pc">Shaifali</Text>
          </VStack>
        </HStack>
      </HStack>
    </Flex>
  );
};